.calendar-day {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 14vw;
    border: solid;
    // margin: 2vw;
    border-radius: 6px;
    background-color: white;
    box-shadow: 0px 2px 3px 0px grey;
    transition: box-shadow 0.5s;
    &:hover {
        box-shadow: 0px 2px 5px 2px grey;
    }
}

.details {
    display: block;
}

.details-hidden {
    display: none;
}

.recipe-button {
    width: 7vw;
    border-radius: 6px;
    border: none;
    cursor: pointer;
    box-shadow: 0px 2px 3px 0px;
}

.calendar-item {
    background-color: rgba(0, 255, 255, 1);
    margin: 0.25vw;
    border-radius: 6px;
}

.calendar-item-hidden {
    display: none;
}

@media screen and (max-width: 500px){
    .calendar-day {
        width: 90vw;
    }
}